@media (min-width:$largeDevices){
    .section-home{
        width: 100%;
        position: relative;
        .profile{
            display: flex;
            padding: 10px 5%;
            flex-direction: row;
            .greetings{
                margin-right: 20px;
                flex: 1;
                display: flex;
                flex-direction: column;
                .greet-box{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    align-self: flex-start;
                    p{
                        font-weight: 500;
                        color: $yellow;
                        position: absolute;
                        margin-bottom: 22px;
                    }
                    img{
                        width: 130px;
                    }
                }
                h1{
                    margin-top: 0px;
                    font-weight: bold;
                    font-size: 50pt;
                    margin-bottom: 10px;
                }
                span{
                    flex: none;
                    margin-bottom: 20px;
                    padding: 5px 20px;
                    background: $yellow;
                    width: fit-content;
                    border-radius: 2vh;
                    font-size: 20pt;
                }
                .social-media{
                    display: flex;
                    flex-direction: row;
                    a{

                        // padding: 20px;
                        display: flex;
                        text-align: center;
                        align-items: center;
                        justify-content: center;
                        width: 50px;
                        height: 50px;
                        background: $darGrey;
                        margin-right: 10px;
                        border-radius: 100%;
                        svg{
                            font-size: 15pt;
                            color: $darkWhite;
                        }
                        &:hover{
                            cursor: pointer;
                            -webkit-box-shadow: 0px 0px 26px -9px rgba(0,0,0,0.4);
                            -moz-box-shadow: 0px 0px 26px -9px rgba(0,0,0,0.4);
                            box-shadow: 0px 0px 26px -9px rgba(0,0,0,0.4);
                        }
                        &:active{
                            -webkit-box-shadow: 0px 0px 26px -9px rgba(0,0,0,0.8);
                            -moz-box-shadow: 0px 0px 26px -9px rgba(0,0,0,0.8);
                            box-shadow: 0px 0px 26px -9px rgba(0,0,0,0.8);
                        }
                    }
                }
            }
            .profile-pic{
                align-content: center;
                justify-items: center;
                img{
                    align-self: flex-end;
                    width: 450px;
                    margin-bottom: 20px;
                    animation: flyingAnim 2s alternate ease-in-out infinite;
                }
            }
        }
    }
    .home-footer{
        display: flex;
        img{
            align-self: flex-end;
            margin-left: 20px;
            margin-bottom: 20px;
        }
        .tools-language{

            margin-left: auto;
            display: flex;
            flex-direction: column;
            margin-left: 50px;
            .tools-slider{
                width: 70%;
                overflow: hidden;
                .items-skills{
                    display: flex;
                    list-style: none;
                    animation: marquee 60s linear infinite;
                    li{
                        margin: 0px 30px;
                        img{
                            transform: scale(1);
                        }
                    }
                    &:hover{
                        cursor: pointer;
                        animation-play-state: paused;
                    }
                }
                // &::before{
                //     content:"";
                //     height: 60px;
                //     width: 30px;
                //     position: absolute;
                //     background: linear-gradient((to right,  rgba(240,240,240,1) 0%,rgba(240,240,240,0) 100%));
                //     left: 159px;
                //     bottom: 32px;
                //     z-index: 4;
                // }
                // &::after{
                //     content:"";
                //     height: 60px;
                //     width: 30px;
                //     position: absolute;
                //     background: linear-gradient((to left,  rgba(240,240,240,1) 0%,rgba(240,240,240,0) 100%));
                //     right: 320px;
                //     bottom: 29px;
                // }
            }
        }
    }
}


  @keyframes flyingAnim {
    0% {
        transform: translateY(0px);
    }
    100% {
        transform: translateY(20px);
    }
  }
  @keyframes marquee{
        0%{
            transform: translate(-102%);
        }
        100%{
            transform: translate(102%);
        }
        0%{
            transform: translate(-100%);
        }
   }

